
import React from 'react';
import { List } from 'antd-mobile';

export default class UserItem extends React.Component {

  render() {
    const {user} = this.props;

    return (
      <List.Item>
        {user.name}
        <br />
        {user.role}
        <br />
        {user.phone}
      </List.Item>
    );
  }
}



